<template>
  <div>
    <div class="loader-wrapper">
      <div class="lds-ring">
        <div></div>
        <div></div>
        <div></div>
        <div></div>
      </div>
    </div>
    <div class="card card-authentication1 mx-auto my-5">
      <div class="card-body">
        <div class="card-content p-2">
          <div class="text-center">
            <img src="./../../../public/assets/images/test.png" alt="logo icon">
          </div>
          <div class="card-title text-uppercase text-center py-3">Sign In</div>
          <form type="post" @submit.prevent="login">
            <div class="form-group">
              <div class="alert alert-danger">
                <ul v-for="err in errors" :key="err">
                  <li>{{ err }}</li>
                </ul>
              </div>
              <label for="exampleInputUsername" class="sr-only">Username</label>
              <div class="position-relative has-icon-right">
                <input type="text" id="exampleInputUsername" name="username"
                       class="form-control input-shadow" placeholder="Enter Phone Number">
                <div class="form-control-position">
                  <i class="icon-user"></i>
                </div>
              </div>
            </div>
            <div class="form-group">
              <label for="exampleInputPassword" class="sr-only">Password</label>
              <div class="position-relative has-icon-right">
                <input type="password" id="exampleInputPassword" name="password"
                       class="form-control input-shadow" placeholder="Enter Password">
                <div class="form-control-position">
                  <i class="icon-lock"></i>
                </div>
              </div>
            </div>
            <button type="submit" class="btn btn-light btn-block">Sign In</button>
          </form>
        </div>
      </div>
      <div class="card-footer text-center py-3">
        <p class="text-warning mb-0">You don't have an account? <router-link to="register"> Sign In here</router-link></p>
      </div>
    </div>
  </div>
</template>

<script>

export default {
  name: "User-Login-page",
  data () {
    return {
      username: '',
      password: '',
      errors: []
    }
  },
  methods: {
    async login(data) {
      this.username = data.target.username.value
      this.password = data.target.password.value
      await this.$axios.post('login', {
        phone: this.username,
        password: this.password
      }).then(response => {
        this.$store.commit('LOGIN_CLIENT', response.data.data)
        this.$axios.defaults.headers.common.Authorization = `Bearer ${response.data.data.token}`
        this.$router.push({ name: 'User.Dashboard' })
      }).catch((err) => this.$notify({text: err.response.data.data.message, type: 'error'}))
    }
  }
}
</script>

